import React, { useEffect, useRef } from "react";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

// Fix Leaflet's default icon paths
import iconUrl from "leaflet/dist/images/marker-icon.png";
import iconRetinaUrl from "leaflet/dist/images/marker-icon-2x.png";
import shadowUrl from "leaflet/dist/images/marker-shadow.png";

// Override the default icon to use the imported assets
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl,
  iconUrl,
  shadowUrl,
});

function MyMapComponent(props) {
  const mapRef = useRef(null);

  // 1) Read marker data from Python
  const markers = props.args.data || [
    // Fallback if no data passed
    { name: "Default Marker", lat: 48.1371, lon: 11.5754 },
  ];

  useEffect(() => {
    // Create the map only once
    if (!mapRef.current) {
      // Initialize Leaflet map
      mapRef.current = L.map("myLeafletMap").setView([48.1371, 11.5754], 12);

      // Add tile layer (OpenStreetMap)
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: "© OpenStreetMap contributors",
      }).addTo(mapRef.current);

      // For each marker in data, create a Leaflet marker
      markers.forEach((m) => {
        const marker = L.marker([m.lat, m.lon]).addTo(mapRef.current);
        marker.bindTooltip(m.name);

        // When clicked, send data back to Python
        marker.on("click", () => {
          Streamlit.setComponentValue({
            clickedName: m.name,
            lat: m.lat,
            lon: m.lon,
          });
        });
      });
    }

    // Ask Streamlit to resize the iframe if needed
    Streamlit.setFrameHeight(window.innerHeight);
  }, [markers]);

  return <div id="myLeafletMap" style={{ width: "100%", height: "100%" }} />;
}

// Wrap the component with "withStreamlitConnection" so we can receive props from Python
export default withStreamlitConnection(MyMapComponent);
